import styled from 'styled-components';
import colors from '../../utils/styles/colors';

export const TableContainer = styled.div`
  overflow-x: auto;

  table {
    width: 100%;
    min-width: max-content;
    border-spacing: 0;
    border-collapse: collapse;
    table-layout: fixed;

    thead {
      background-color: ${colors.secondary};
      font-size: 13px;
      font-weight: 400;

      tr {
        th {
          padding: 18px 24px;
          text-align: left;
        }

        @media (max-width: 600px) {
          border-bottom: 3px solid red;
          display: block;
          margin-bottom: 0.625em;
        }
      }

      @media (max-width: 600px) {
        border: none;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
      }
    }

    tbody {
      tr {
        td {
          padding: 16px 24px;
          text-align: left;
          font-size: 13px;
          font-weight: 400;

          @media (max-width: 600px) {
            border-bottom: 1px solid ${colors.light_grey};
            display: block;
            font-size: 0.8em;
            text-align: right;

            &::before {
              content: attr(data-label);
              float: left;
              font-weight: bold;
            }
          }
        }

        @media (max-width: 600px) {
          display: block;
          margin-bottom: 32px;
          border-bottom: 3px solid ${colors.light_grey};
        }
      }
    }
  }
`;
