import React, { useEffect, useMemo } from 'react';
import * as Yup from 'yup';

import Form from 'components/Form';
import Field from 'components/Form/Field';
import Modal from 'components/Modal';
import Button from 'components/Button';
import Select from 'components/Form/Select';

import { useDispatch, useSelector } from 'react-redux';
import { IState } from 'store';
import { ButtonsContainer } from './styles';
import {
  createUserRequest,
  deletetUserRquest,
  editUserRequest,
} from '../../states/actions';
import { IUser, IUsersState } from '../../states/types';

interface UsersFormProps {
  isShowing: boolean;
  toggle(): void;
  initialValues?: object;
}
const UsersForm: React.FC<UsersFormProps> = ({
  isShowing,
  toggle,
  initialValues,
}) => {
  const formSchema = useMemo(
    () =>
      Yup.object().shape({
        nome: Yup.string().required('Campo obrigatrio'),
        permissao: Yup.string().required('Campo obrigatrio'),
        login: Yup.string()
          .required('Campo obrigatrio')
          .email('Email invlido'),
        ...(initialValues
          ? {
              senha: Yup.string()
                .min(6, 'Minimo de 6 caracteres')
                .max(50, 'Mximo de 50 caracteres'),
            }
          : {
              senha: Yup.string()
                .required('Campo obrigatrio')
                .min(6, 'Minimo de 6 caracteres')
                .max(50, 'Mximo de 50 caracteres'),
            }),
      }),
    [initialValues],
  );

  const v = useSelector<IState, IUsersState>(state => {
    return state.users;
  });

  useEffect(() => {
    console.log(v);
  }, [v]);

  const dispatch = useDispatch();

  const onSubmit = (values: IUser) => {
    if (values.id) dispatch(createUserRequest(values));
    else dispatch(editUserRequest(values));
  };

  return (
    <Modal
      isShowing={isShowing}
      hide={toggle}
      title={initialValues ? 'Edio de usurio' : 'Adicionar novo usurio'}
      okText="Salvar"
      hideCloseButton
      hideOkButton
      handleOk={toggle}
    >
      <Form
        initialValues={initialValues}
        onSubmit={(values: IUser) => onSubmit(values)}
        validationSchema={formSchema}
      >
        <Field
          id="idUsuario"
          name="idUsuario"
          label="Id do usurio"
          type="number"
          visible={false}
        />

        <Field
          id="nome"
          name="nome"
          label="Nome completo"
          placeholder="Digite o nome"
          maxLength={50}
        />

        <Field
          id="login"
          name="login"
          label="E-mail"
          placeholder="Digite o e-mail"
        />

        <Select
          id="permissao"
          name="permissao"
          label="Permissso"
          options={[
            { label: 'Administrador', value: 'Administrador' },
            { label: 'Atendente', value: 'Atendente' },
            { label: 'Profissional', value: 'Profissional' },
          ]}
        />

        <Field
          id="senha"
          name="senha"
          label="Senha"
          type="password"
          placeholder="Crie uma senha"
        />

        <Field
          id="confirmacaoSenha"
          name="confirmacaoSenha"
          label="Confirmar senha"
          type="password"
          placeholder="Confirme a senha"
        />

        <ButtonsContainer>
          <Button
            type="submit"
            text={initialValues ? 'Salvar alteraes' : 'Adicionar'}
          />
        </ButtonsContainer>
      </Form>
    </Modal>
  );
};

export default UsersForm;
