import React, { useState } from 'react';

import useModal from 'hooks/modal';

import Button from 'components/Button';
import Table from 'components/Table';
import DeleteIcon from 'assets/svg/delete-icon.svg';
import EditIcon from 'assets/svg/edit-icon.svg';
import UsersForm from '../components/UsersForm';

import { Container, Header, ContainerTable } from '../components/styles';

const Users: React.FC = () => {
  const [selectedUser, setselectedUser] = useState<object>();
  const { isShowing: modalFormVisible, toggle: toggleModalForm } = useModal();

  const handleClickAddButton = () => {
    setselectedUser(undefined);
    toggleModalForm();
  };

  const handleClickEditButton = (row: object) => {
    setselectedUser(row);
    toggleModalForm();
  };

  return (
    <>
      <Container>
        <Header>
          <h1>Usurios</h1>

          <Button text="+ Adicionar novo" onClick={toggleModalForm} />
        </Header>

        <ContainerTable>
          <Table
            columns={[
              { Header: 'Id', accessor: 'id' },
              { Header: 'Nome', accessor: 'nome' },
              { Header: 'EMail', accessor: 'login' },
              {
                Header: 'Aes',
                accessor: 'acoes',
                Cell: ({ row: { original } }) => (
                  <>
                    <button
                      type="button"
                      onClick={() => handleClickEditButton(original)}
                    >
                      <img src={EditIcon} alt="Editar" />
                    </button>

                    <button type="button">
                      <img src={DeleteIcon} alt="Editar" />
                    </button>
                  </>
                ),
              },
            ]}
            data={[
              {
                id: 1,
                nome: 'Carolina Castro',
                login: 'carol.castro@gmail.com',
              },
              {
                id: 2,
                nome: 'Carolina Almeida',
                login: 'carol.almeida@gmail.com',
              },
              { id: 3, login: 'carol.alves@gmail.com', nome: 'Carolina Alves' },
              { id: 4, login: 'carol.silva@gmail.com', nome: 'Carolina Silva' },
            ]}
            hiddenColumns={['id']}
          />
        </ContainerTable>
      </Container>

      <UsersForm
        isShowing={modalFormVisible}
        toggle={handleClickAddButton}
        initialValues={selectedUser}
      />
    </>
  );
};

export default Users;
